@forward 'global';


#content-box{
  @extend .wrapper;
  display: grid;
  grid-template-columns: 15vw 82vw;
  grid-template-rows: 90dvh;
  gap: 15px;
  
  >div{
    padding-top: 5px;
    padding-left: 5px;
  }
}

#tree, #content{
  overflow-y: scroll;
  font-weight: normal;

  background-color: rgba($color: #000000, $alpha: 0.3);
  border: solid black;
  border-radius: 5px;
  box-shadow: black 3px 3px 6px 1px;
  &::-webkit-scrollbar{
    width: 5px;
    height: 5px;
    //background-color: rgba(199, 199, 199, 0.151);
    
  }
  
  &::-webkit-scrollbar-track{
    border-radius: 10px;
  }
  
  &::-webkit-scrollbar-thumb{
    background-color: #1c1c1c;
    border-radius: 10px;
  }

}
